body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: 16px;
  line-height: 22px;
  color: #102654; }

a {
  text-decoration: none; }

img {
  width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 19px;
  box-sizing: border-box; }

@media (max-width: 1199px) {
  .container {
    padding: 0 45px; } }

@media (max-width: 767px) {
  .container {
    padding: 0 15px; } }

.logo img {
  width: 89px;
  height: 20px; }

@media (max-width: 1199px) {
  .logo img {
    width: 82px;
    height: 17px; } }

@media (max-width: 767px) {
  .logo img {
    width: 65px;
    height: 15px; } }

.button {
  display: block;
  padding: 20px;
  font-weight: bold;
  font-size: 12px;
  line-height: 14px;
  color: #ffffff;
  align-items: center;
  letter-spacing: 0.05em;
  text-align: center;
  text-transform: uppercase;
  border: 4px solid #ffffff;
  box-sizing: border-box; }
  .button:hover, .button:focus {
    text-decoration: underline;
    box-shadow: 2px 2px rgba(255, 255, 255, 0.5); }
  .button:active {
    text-decoration: none;
    opacity: 0.8; }

.head {
  min-height: 658px;
  background-color: #b4042c;
  background-image: url("../img/bg-head-desktop@1x.jpg");
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: cover; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .head {
      background-image: url("../img/bg-head-desktop@2x.jpg"); } }

@media (max-width: 1199px) {
  .head {
    min-height: 554px;
    background-image: url("../img/bg-head-tablet@1x.jpg"); } }
  @media (max-width: 1199px) and (min-resolution: 144dpi), (max-width: 1199px) and (min-resolution: 1.5dppx) {
    .head {
      background-image: url("../img/bg-head-tablet@2x.jpg"); } }

@media (max-width: 767px) {
  .head {
    min-height: 491px;
    background-image: url("../img/bg-head-mobile@1x.jpg"); } }
  @media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
    .head {
      background-image: url("../img/bg-head-mobile@2x.jpg"); } }

.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding-top: 30px;
  padding-bottom: 30px; }

.page-header__phone {
  margin-top: -2px;
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-size: 26px;
  line-height: 26px;
  letter-spacing: 0.03em;
  color: #ffffff; }
  .page-header__phone:hover, .page-header__phone:focus {
    opacity: 0.8; }
  .page-header__phone:active {
    opacity: 1; }

@media (max-width: 1199px) {
  .page-header {
    padding-top: 40px;
    padding-bottom: 40px; }
  .page-header__phone {
    font-size: 24px;
    line-height: 24px; } }

@media (max-width: 767px) {
  .page-header {
    padding-top: 20px;
    padding-bottom: 20px; }
  .page-header__phone {
    font-size: 20px;
    line-height: 20px; } }

.main-block {
  position: relative;
  display: flex;
  flex-wrap: wrap; }

.main-block__content {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: 397px;
  margin-top: 82px; }

.main-block__city-text {
  margin-bottom: 12px;
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-size: 49px;
  line-height: 49px;
  letter-spacing: 0.03em;
  color: #ffffff;
  text-transform: uppercase; }

.main-block h1 {
  margin: 0;
  margin-bottom: 57px;
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-size: 109px;
  line-height: 109px;
  text-align: right;
  letter-spacing: 0.03em;
  color: #ffffff;
  text-transform: uppercase; }

.main-block__list,
.main-block__social {
