.main-block__image {
  width: 500px;
  height: 556px;
  margin-top: -34px;
  margin-left: 64px;
  background-color: #073470;
  background-image: url("../img/bg-main-block-desktop@1x.jpg");
  background-repeat: no-repeat;
  background-position: 0 0; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
